import React, { useState, useEffect } from 'react';
import QRScanner from './components/QRScanner';
import Quiz from './components/Quiz';
import './components/App.css';

const App = () => {
  const [scannedData, setScannedData] = useState(null);
  const [existingData, setExistingData] = useState([]);
  const [showScanner, setShowScanner] = useState(false); // QRScannerを表示するための状態
  const [inputValue, setInputValue] = useState(''); // 入力フィールドの状態
  const [showInput, setShowInput] = useState(false); // 入力フィールドの表示状態

  useEffect(() => {
    const savedData = localStorage.getItem('quizData');
    if (savedData) {
      setExistingData(JSON.parse(savedData));
    }
  }, []);

  const handleDataReceived = (data) => {
    try {
      console.log("parsed data:", data);
      const newQuestionId = data.question[0].id;

      // 新しいデータのidが存在するか確認
      const idExists = existingData.some(item => item.question[0].id === newQuestionId);

      if (!idExists) {
        // 新しいデータを追加
        const updateData = [...existingData, data];

        // ローカルストレージに更新されたデータを保存
        localStorage.setItem('quizData', JSON.stringify(updateData));
        setScannedData(data);
      } else {
        alert("すでに読み込んだデータです");
        console.log("id already exists");
      }

    } catch (err) {
      console.error("json parse error:", err);
      alert("QRコードのデータが無効です");
    }
    setShowScanner(false); // QRScannerを非表示にする
  };

  const handleQRSwitch = () => {
    setShowScanner(prevState => !prevState); // QRScannerの表示状態をトグルする
  };

  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleClearLocalStorage = () => {
    if (inputValue === 'get') { // 特定の語句と一致する場合にローカルストレージを削除
      localStorage.removeItem('quizData');
      localStorage.removeItem('correctQuestionIds');
      setScannedData(null);
      setExistingData([]);
      alert('おつかれさまでした');
    } else {
      alert('無効な語句です');
    }
  };

  const handleShowInput = () => {
    setShowInput(true); // 入力フィールドを表示する
  };

  return (
    <div className="container">
      <h1>QRクイズラリー</h1>
      <button className="scannerbutton" onClick={handleQRSwitch}>
        {showScanner ? 'カメラをとじる' : 'QRコードをスキャン'}
      </button>
      {showScanner && <QRScanner onDataReceived={handleDataReceived} />}
      {scannedData && <Quiz data={scannedData} />}
      {showInput ? (
        <>
          <input
            type="text"
            value={inputValue}
            onChange={handleInputChange}
            placeholder="景品交換はK1-815で"
          />
          <button className="exchangebutton" onClick={handleClearLocalStorage}>完了!</button>
        </>
      ) : (
        <button className="exchangebutton" onClick={handleShowInput}>景品を交換する</button>
      )}
    </div>
  );
};

export default App;